import React from "react";
import Home from "./Home";
import Task from "./Task";
import Evaluation from "./Evaluation";
import Monitoring from "./Monitoring";
import Importing from "./Importing";
import ProjectWorkLog from "./ProjectWorkLog";
import QA from "./QA";

const Main = ({ activeComponent }) => {
  const renderComponent = () => {
    switch (activeComponent) {
      case "Home":
        return <Home />;
      case "Task":
        return <Task />;
      case "Evaluation":
        return <Evaluation />;
      case "Monitoring":
        return <Monitoring />;
      case "Importing":
        return <Importing />;
      case "ProjectWorkLog":
        return <ProjectWorkLog />;
      case "QA":
        return <QA />;
      default:
        return <Task />;
    }
  };

  return <main>{renderComponent()}</main>;
};

export default Main;
