import React from "react";
import PropTypes from "prop-types";

const Table = ({ data, columns, colorConfig }) => {
  if (!data || data.length === 0) {
    return <p>No data available</p>; // Hiển thị nếu không có dữ liệu
  }

  const getBackgroundColor = (columnKey, value, row) => {
    if (colorConfig && colorConfig[columnKey]) {
      const { red, green } = colorConfig[columnKey];
      if (red && red(value, row)) return "red";
      if (green && green(value, row)) return "green";
    }
    return "transparent"; // Màu nền mặc định
  };

  const getTextColor = (columnKey, value, row) => {
    if (colorConfig && colorConfig[columnKey]) {
      const { textWhite } = colorConfig[columnKey];
      if (textWhite && textWhite(value, row)) return "white";
    }
    return "black"; // Màu text mặc định
  };

  return (
    <div style={{ maxHeight: "500px", overflowY: "auto" }}>
      <table className="table table-bordered">
        <thead>
          <tr>
            {/* Render các tiêu đề từ prop columns */}
            {columns.map((column) => (
              <th key={column.key}>{column.header}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {/* Render từng hàng dữ liệu */}
          {data.map((row, rowIndex) => (
            <tr key={rowIndex}>
              {columns.map((column) => (
                <td
                  key={column.key}
                  style={{
                    backgroundColor: getBackgroundColor(column.key, row[column.key], row),
                    color: getTextColor(column.key, row[column.key], row),
                  }}
                >
                  {column.key === "TaskName" ? (
                    <span className="d-inline-block text-truncate" style={{ maxWidth: "50px" }}>
                      {row[column.key]}
                    </span>
                  ) : (
                    row[column.key]
                  )}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

// Xác định kiểu dữ liệu cho component Table
Table.propTypes = {
  data: PropTypes.arrayOf(PropTypes.object).isRequired, // Dữ liệu là một mảng object JSON
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      header: PropTypes.string.isRequired, // Tên cột hiển thị
      key: PropTypes.string.isRequired, // Key trong dữ liệu JSON
    })
  ).isRequired,
  colorConfig: PropTypes.object, // Cấu hình màu sắc cho các cột
};

export default Table;
