import React, { useEffect, useState } from "react";
import { ButtonWithIcon } from "../ui/Button";

const Importing = () => {
  const shNameS = "ExternalData";
  const tbNameS = "TblExternalData";
  const kNameS = "キー";
  const lcNameS = ["キー", "カテゴリー名", "親課題キー", "開始日", "期限日", "期限日"];
  const shNameT = "WBS";
  const tbNameT = "TblWbs";
  const kNameT = "TaskID";
  const lcNameT = ["TaskID", "TaskName", "ParentID", "P.StartDate", "P.EndDate", "P.ReleaseDate"];

  const syncData = async (
    SheetNameSource,
    TableNameSource,
    KeyColumnNameSource,
    ListColumnNameSource,
    SheetNameTarget,
    TableNameTarget,
    KeyColumnNameTarget,
    ListColumnNameTarget
  ) => {
    return Excel.run((context) => {
      const sourceSheet = context.workbook.worksheets.getItem(SheetNameSource);
      const sourceTable = sourceSheet.tables.getItem(TableNameSource);
      const targetSheet = context.workbook.worksheets.getItem(SheetNameTarget);
      const targetTable = targetSheet.tables.getItem(TableNameTarget);

      // Load the data from source and target tables
      const sourceRange = sourceTable.getDataBodyRange();
      const targetRange = targetTable.getDataBodyRange();

      sourceRange.load("values");
      targetRange.load("values");

      // Load header rows
      const sourceHeaderRange = sourceTable.getHeaderRowRange();
      const targetHeaderRange = targetTable.getHeaderRowRange();
      sourceHeaderRange.load("values");
      targetHeaderRange.load("values");

      return context
        .sync()
        .then(() => {
          const sourceData = sourceRange.values;
          const targetData = targetRange.values;
          const targetKeys = new Set(targetData.map((row) => row[0])); // Assuming KeyColumnNameTarget is the first column

          // Get indexes for columns
          const headersSource = sourceHeaderRange.values[0];
          const headersTarget = targetHeaderRange.values[0];
          const keyColIndexSource = headersSource.indexOf(KeyColumnNameSource);
          const listColIndexesSource = ListColumnNameSource.map((col) => headersSource.indexOf(col));
          const keyColIndexTarget = headersTarget.indexOf(KeyColumnNameTarget);
          const listColIndexesTarget = ListColumnNameTarget.map((col) => headersTarget.indexOf(col));

          const newRows = [];

          // Loop through source data to find new entries
          sourceData.forEach((row) => {
            const keyValue = row[keyColIndexSource];

            if (!targetKeys.has(keyValue)) {
              let newRow = new Array(headersTarget.length).fill("");
              listColIndexesSource.forEach((sourceIndex, index) => {
                const targetIndex = listColIndexesTarget[index];
                newRow[targetIndex] = row[sourceIndex];
              });
              newRows.push(newRow);
            }
          });

          // If there are new rows to insert, add them to the target table
          if (newRows.length > 0) {
            targetTable.rows.add(null, newRows);
            return context.sync().then(() => {
              console.log("Mapping completed. New rows inserted:", newRows.length);
            });
          } else {
            console.log("No new rows to insert.");
          }
        })
        .catch((error) => {
          console.error(error);
        });
    });
  };
  const handleSync = () => {
    syncData(shNameS, tbNameS, kNameS, lcNameS, shNameT, tbNameT, kNameT, lcNameT);
  };
  return <ButtonWithIcon label={"Sync"} icon={"arrow-repeat"} onClick={handleSync} />;
};

export default Importing;
