import React from "react";
import PropTypes from "prop-types";

// Component Emoji nhận 'name' là một props để render emoji tương ứng
const Emoji = ({ name }) => {
  // Danh sách emoji tương ứng với tên
  const emojiList = {
    smile: "😄",
    sad: "😢",
    laugh: "😂",
    angry: "😠",
    love: "🥰",
    surprised: "😱",
    dead: "💀",
    crying: "😭",
    party: "🥳",
    rocket: "🚀",
    thumbs_up: "👍",
    heart: "❤️",
  };

  // Kiểm tra nếu emoji tồn tại trong danh sách
  const emoji = emojiList[name] || "❓"; // Nếu không tìm thấy, trả về dấu hỏi ❓

  return (
    <span role="img" aria-label={name} aria-hidden={emoji === "❓"}>
      {emoji}
    </span>
  );
};

// PropTypes để kiểm tra kiểu dữ liệu cho props
Emoji.propTypes = {
  name: PropTypes.string.isRequired,
};

export default Emoji;
